        p(data-aos="fade-down") lvarez, Marcos (2016) dice que <i>Customer Experience</i> (CX) o la experiencia de cliente es una estrategia que se convirti en la actualidad en el genuino diferenciador entre los negocios y en una pieza clave del <i>customer centricity</i> o centricidad en el cliente. Es as que personifica una ventaja competitiva nica, porque casi todas las ventajas racionales bsicas ofrecidas por las marcas, son dadas por parte del cliente como si fueran un hecho. Por esta razn, el cliente piensa que el producto es apto para la compra respondiendo a sus necesidades, as como que el producto llegar justo a tiempo y que en caso de tener problemas se le atender de la mejor forma. 

        p(data-aos="fade-down") La experiencia de cliente es la imagen que un cliente posee de las interacciones vividas con una marca. Por eso, las interacciones se pueden producir en distintos momentos de la transaccin comercial, como, por ejemplo:

    img.img-a.img-t.mb-5.d-none.d-lg-block(data-aos="zoom-in")(src='@/assets/curso/temas/img3.svg', alt='')  
