        p(data-aos="fade-down") La experiencia del cliente es la imagen general que tiene el cliente de todos esos momentos durante el <i>Customer Journey</i> (o sea, el viaje realizado por el cliente para realizar una accin de compra).

        p(data-aos="fade-down").mb-0 Es difcil que las marcas puedan conservar el liderazgo diferencindose con el producto o a travs de su costo, durante mucho tiempo (debido a la competencia). Con la economa a gran escala, la produccin industrial y el ahorro de costos, es normal que existan marcas que lo hagan igual y ms econmico. Sin embargo, el cliente supone que estn a la orden del da temas relevantes como el servicio, el precio y la calidad. 

