        p.p-4(data-aos="fade-down").mb-0 Por lo tanto, si las expectativas racionales no destacan a una marca, Qu es lo que le permite hacerlo? La respuesta est en cmo hacer sentir al cliente. Se vive una era donde las marcas luchan por las emociones, y ah es vital la experiencia del cliente. Es por esta razn que la clave es tener un enfoque centrado en el cliente, para motivarlo y fidelizarlo. 

    .row.justify-content-center.align-items-center.mb-5 
      .col-lg-8.my-3
        p(data-aos="fade-down") En cada uno de los momentos de interaccin que la marca tiene con el cliente sirven para generar emociones concretas, las cuales se van sumando de a poco hasta lograr una experiencia positiva. Todo tiene que estar perfectamente encadenado. Por ejemplo, es posible que la accin de compra haya sido exitosa, pero en el momento de la asesora algo fall. Es as como, cada uno de estos momentos irn sumando percepciones hasta lograr una opinin propia, la cual determina la percepcin general de cada uno.

        p(data-aos="fade-down") Como se trata de percepciones emocionales, cada cliente siente de forma subjetiva lo que experiment, y eso termina con la generacin de una opinin que evolucionar a un comportamiento. No se debe dejar nada al azar, y para evitar la incertidumbre de percepciones por parte de los clientes, lo ideal es brindar una experiencia de cliente slida y permanente. Todos los empleados de la empresa que tengan contacto con el cliente deben estar alineados, da a da; igual ocurre con las estrategias y sus planes de accin porque todo debe salir perfecto, solo as es ms fcil de administrar y mejorar (en el momento que sea necesario). No se puede dejar a un lado la individualidad del cliente y su personalizacin. 
