        .row.justify-content-center.align-items-start.p-3
          .col-lg-6.mb-4.mb-md-0
            h4 Mantener una identidad nica
            p No significa siempre hacer las mismas cosas que las dems marcas del sector. La estrategia de experiencia de cliente debe estar orientada a todo aquello que desea el cliente y, adems, superar con creces esas expectativas. No se recomienda repetir planes anteriores, se sugiere intentar sobresalir en las ideas de manera original y creativa, porque de lo contrario, la marca ser una ms.
        
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img15.png', alt='')    
        .row.justify-content-center.align-items-start.p-3
          .col-lg-6.mb-4.mb-md-0
            h4 Lograr impulso organizativo 
            p Toda la organizacin es responsable de crear, desarrollar y mantener  una CX,  aunque s debe existir un responsable, puede ser un gerente comercial o un experto en mercadeo. Es cuando entra en accin la figura del <i>Chief Customer Officer</i>, o sea, quien se responsabiliza de la CX. Es un cargo vital, ya que, en la mayor parte de los casos, los negocios deben implementar una estrategia dentro de su cotidianidad. La funcin principal es el diseo y administracin del CX en su versin transversal a travs de la empresa. Es quien debe preocuparse por los momentos de la verdad en cada uno de los <i>touchpoint</i> (punto o momentos de contacto) que la marca tenga con el cliente.  
        
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img16.png', alt='') 
        .row.justify-content-center.align-items-start.p-3
          .col-lg-6.mb-4.mb-md-0
            h4 Involucrar a los empleados y conseguir que se involucren naturalmente 
            p Para desarrollar la estrategia de CX se necesita contar con los empleados del negocio. Al ser un proceso que debe pasar por varias etapas, lo ideal es que el CX incluya todos los departamentos, y por eso, es vital que todos los empleados tengan credibilidad y confianza en la estrategia. Tcticas como alinear las polticas de Recursos Humanos, informar y capacitar a toda la empresa o proporcionar importancia a los empleados en el disear de la estrategia. Los primeros clientes de una marca son siempre los empleados; a travs de ellos es como se logra el xito, y es donde entra en juego el concepto de <i>Employee Experience</i> (Experiencia del Empleado) que tambin es vital tenerlo en cuenta.  
        
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img17.png', alt='')
        .row.justify-content-center.align-items-start.p-3
          .col-lg-6.mb-4.mb-md-0
            h4 Generar interacciones de los productos
            p La generacin de interacciones de los productos con el cliente es vital, y ms an, si luego de ello se logra la recomendacin. El #[i marketing] viral en todo su esplendor, logrando que los mismos usuarios recomienden la marca a sus contactos. Esto se logra cuando se tienen clientes que han vivido una CX inolvidable. Actualmente, existen un mundo de productos parecidos, un mundo de vendedores ofreciendo lo mismo. Por eso, ser diferente es una opcin que puede ser viable para los ms giles en su relacin con el cliente. El comercio electrnico y el #[i marketing] digital deben ser aprovechados al mximo para generar interacciones con los clientes y posteriormente las recomendaciones de ellos a sus contactos, usando herramientas como el correo electrnico, las redes sociales, el sitio <i>web</i> y el blog, entre otras.  
        
