              .tarjeta-foto-hover__titulo.d-flex.justify-content-center.align-items-center.p-2
              .tarjeta-foto-hover__tarjeta.bg-degrade.d-flex.align-items-center.justify-content-center
                .p-4.text-center
                  p.mb-0 Poder evaluar el retorno de la inversin (ROI) de futuras inversiones en la Experiencia del usuario y del cliente.

    .row.justify-content-start.align-items-center.mb-5           
      .col-auto
        .list.bg2(data-aos="fade-down")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/v.svg', alt='')
          .list__txt
            h3.mb-0.mt-1.pe-3 Pasos para crear un mapa de la experiencia del cliente 

    .row.justify-content-center.align-items-center.mb-2           
      .col-lg-2.my-3 
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img38.svg', alt='')   
      .col-lg-10.my-3
        p(data-aos="fade-down") Para poder disear un mapa de la experiencia del cliente se requiere conocerlo profundamente. Este nivel de alta comprensin significa que la marca debe conocer la experiencia de su cliente. Una vez conocido lo que el cliente siente, observa, oye y toca en cada interaccin, se tiene la base para comenzar a disear un mapa y medir la UX.

        p(data-aos="fade-down") Se puede disear un mapa de la UX siguiendo estos pasos: 

    .tarjeta.p-4.mb-5.bg6
      LineaTiempoC.color-acento-contenido(text-small)(data-aos="fade-right")
        .row.justify-content-lg-center.align-items-start(titulo="Paso 1")
          .col-md-4.col-10
            figure
              img(src='@/assets/curso/temas/img39.png')
          .col-md-7.col-10.mb-4.mb-md-0
            h4.mt-3 Paso 1: Realizar una investigacin. 
            p Antes de comenzar el diseo del mapa es vital conocer el cliente. Es as como se puede conseguir la informacin requerida para continuar con el proceso. Las encuestas son herramientas que ayudan mucho para saber quin es y cul es su necesidad en el mercado.  
        .row.justify-content-lg-center.align-items-start(titulo="Paso 2")
          .col-md-4.col-10
            figure
              img(src='@/assets/curso/temas/img40.png')
          .col-md-7.col-10.mb-4.mb-md-0
            h4.mt-2 Paso 2: Seleccionar el #[i Buyer] persona.
            p Poseer un #[i Buyer] Persona ayuda a tener una representacin ideal del cliente final de la marca. Se puede disear basndose en datos demogrficos, actitudes, hbitos, necesidades y deseos. De esta manera, es ms fcil ponerse en la piel del pblico objetivo. Usar un #[i Buyer] Persona permite alcanzar los objetivos empresariales y aumentar las ventas.  
        .row.justify-content-lg-center.align-items-start(titulo="Paso 3")
          .col-md-4.col-10
            figure
              img(src='@/assets/curso/temas/img41.png')
          .col-md-7.col-10.mb-4.mb-md-0
            h4.mt-3 Paso 3: Identificar los puntos de contacto con el cliente.
            ul.lista-ul
              li 
                i.fas.fa-angle-right.color-v
                p.mb-0 #[b Contacto antes de encontrar tu negocio:] preguntar al cliente en qu medio encontr la empresa, marca o productos.

            ul.lista-ul
              li 
                i.fas.fa-angle-right.color-v
                p.mb-0 #[b Contacto mientras se encuentran en tu negocio:] descubrir por qu visitan la empresa (presencial o virtual), la razn, las necesidades que tuvo y el porqu de la compra.

            ul.lista-ul
              li 
                i.fas.fa-angle-right.color-v
                p.mb-0 #[b Contacto despus de comprar en tu negocio:] conocer cmo fue la experiencia, la marca, si pudo solucionar el problema, si fue bueno el servicio al cliente.              
        .row.justify-content-lg-center.align-items-start(titulo="Paso 4")
          .col-md-4.col-10
            figure
              img(src='@/assets/curso/temas/img42.png')
          .col-md-7.col-10.mb-4.mb-md-0
            h4.mt-3 Paso 4: Responder preguntas orientadoras relacionadas con el cliente.
            ul.lista-ul
              li 
                i.fas.fa-angle-right.color-v
                p.mb-0 A dnde va cuando tiene una necesidad? (La necesidad que la marca resuelve) 
              li 
                i.fas.fa-angle-right.color-v
                p.mb-0 Cmo descubri la solucin a su problema? 
              li 
                i.fas.fa-angle-right.color-v
                p.mb-0 Qu lo motiv a comprar el producto? 
              li 
                i.fas.fa-angle-right.color-v
                p.mb-0 Qu hace cuando requiere algo ms de la empresa, despus de realizar la compra? 
        .row.justify-content-lg-center.align-items-start(titulo="Paso 5")
          .col-md-4.col-10
            figure
              img(src='@/assets/curso/temas/img43.png')
          .col-md-7.col-10.mb-4.mb-md-0
            h4.mt-3 Paso 5: Visualizar el mapa y analizar los resultados.
            p Comprender los objetivos del negocio en general. Se necesita meditar si los clientes satisficieron las necesidades con la compra del producto, en dnde se pueden hacer correcciones, quines son la competencia y en qu momento del <i>Customer Journey</i> los clientes se retiran. Es vital visualizar estos datos, de lo contrario no se podra hacer un anlisis de informacin profunda de cada uno de los puntos registrados en el mapa. 

    Separador 
    #t_1_2.titulo-segundo.color-acento-contenido(data-aos="zoom-in-left")
      h2 1.2  Comunicacin estratgica con clientes

    .row.justify-content-center.align-items-center.mb-5           
      .col-lg-4
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img44.png', alt='')                  
      .col-lg-8
        p(data-aos="fade-down") Haeske & Raabe De Fonrodona (2016) mencionan que escuchando a los clientes es la forma ms segura para saber qu necesitan. Brindar canales o medios que faciliten el contacto con la empresa es vital para el diseo de estrategias comunicativas con el cliente. Lo cierto es que la comunicacin lo es todo en el tema de las interacciones de servicio al cliente. Los expertos tienen que dar uso a sus habilidades y, adems, a herramientas apropiadas para vincularse con los clientes de una forma natural, para que se sientan satisfechos, serenos, confiados y con credibilidad hacia la marca. 

    .row.justify-content-center.align-items-center.mb-5.pt-5           
      .col-lg-10.bg1.p-4.imgs-flotantes.my-3(data-aos="fade-left") 
        img.img-flotante(data-aos="zoom-in")(src='@/assets/curso/temas/img45.svg', alt='Imagen decorativa')
        p.p-4(data-aos="fade-down").mb-0 Siempre se debe seguir un plan de comunicacin poderoso, sin importar si el cliente llega a la marca porque posee algn problema con ella o porque solicita asesora en una compra. Una estrategia de comunicacin con el cliente, diseada de forma oportuna, puede fidelizarlo, adems de agregarle valor para la marca.

    .row.justify-content-start.align-items-center.mb-5.pt-5           
      .col-auto
        .list.bg2(data-aos="fade-down")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/v.svg', alt='')
          .list__txt
            h3.mb-0.mt-1.pe-3 Pasos para disear una estrategia de comunicacin comercial efectiva con el cliente

    p(data-aos="fade-down").mb-5 Una estrategia de comunicacin con el cliente es una ruta de navegacin que formula cmo planear la transmisin de un mensaje especfico al cliente objetivo por medio de distintos canales. Los tres (3) factores primordiales a tener en cuenta siempre en una estrategia de comunicacin con el cliente son: 
             
    .row.justify-content-center.align-items-center.mb-5           
      .col-lg-5.my-3 
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img46.svg', alt='')            
      .col-lg-5.my-3             
        .row.mb-3.justify-content-center
          .col-12.col-lg-12.mb-3.h-100(data-aos="zoom-in")
            .row.justify-content-start
              .col-2.p-0.bg5_1
                figure
                  img.img-a.img-t(src='@/assets/curso/temas/img47.svg', alt='')
              .col-9.p-3.ps-4.bg5
                div
                  h5 Mensaje:
                  p.mb-0 Qu se quiere comunicar.
        .row.mb-3.justify-content-center
          .col-12.col-lg-12.mb-3.h-100(data-aos="zoom-in")
            .row.justify-content-start
              .col-2.p-0.bg5_1
                figure
                  img.img-a.img-t(src='@/assets/curso/temas/img48.svg', alt='')
              .col-9.p-3.ps-4.bg5
                div
                  h5 Objetivo:  
                  p.mb-0 A quin se le desea comunicar.
        .row.mb-3.justify-content-center
          .col-12.col-lg-12.mb-3.h-100(data-aos="zoom-in")
            .row.justify-content-start
              .col-2.p-0.bg5_1
                figure
                  img.img-a.img-t(src='@/assets/curso/temas/img49.svg', alt='')
              .col-9.p-3.ps-4.bg5
                div
                  h5 Medio:  
                  p.mb-0 A travs de qu canales se desea comunicar.

    .row.justify-content-center.align-items-center.mb-5           
      .col-lg-4.my-3
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img50.png', alt='')            
      .col-lg-8.my-3
        p(data-aos="fade-down") Para que la #[b comunicacin sea un xito, los tres (3) factores se deben complementar entre s.] Luego de concretar al pblico meta, de tener claro cul es el mensaje que se desea enviar y de saber qu canales de comunicacin usar, se contina con la estrategia de comunicacin, lo que integra la decisin sobre cmo se divulgarn los productos, cmo administrar la comunicacin con los clientes a travs de los canales elegidos para interactuar con los clientes, cul ser el lenguaje que la marca va a acoger y cul ser la direccin para lograr la satisfaccin de las necesidades de los clientes.

        p(data-aos="fade-down") #[b Poseer un plan de comunicacin estratgico es vital para que el mensaje se pueda transmitir de forma eficiente y precisa], que impida preocupaciones futuras. Es as como se pueden prever las dificultades y problemticas que puedan aparecer en el camino. 

    .row.justify-content-start.align-items-center.mb-5.pt-5           
      .col-auto
        .list.bg2(data-aos="fade-down")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/v.svg', alt='')
          .list__txt
            h3.mb-0.mt-1.pe-3 Cmo tener una comunicacin efectiva con el cliente

    p(data-aos="fade-down").mb-5 Tener una comunicacin efectiva hace parte de la satisfaccin del cliente y por eso, se debe certificar que se haga realidad. Los siguientes son algunas recomendaciones para lograrlo: 

    .row.justify-content-center.mb-5.pb-0.pb-lg-5
      .col-10.col-lg.m-2.m-lg-2(data-aos="fade-left")
        img.img-a3.d-none.d-lg-block(src='@/assets/curso/temas/img51.svg')
        .row.h-100
          .col-12.px-5.pt-5.pb-5.pb-lg-0.bg7
            h4.mb-3 Descubrir lo que los clientes quieren y medir su satisfaccin.  
            p.mb-0 Escuchar a los clientes actuales es una de las excelentes prcticas de la atencin al cliente. Y qu mejor forma de hacerlo que desarrollando un anlisis de su nivel de satisfaccin con la marca. La medicin de la satisfaccin del cliente permite la identificacin de las fortalezas y debilidades de la estrategia, adems de mostrar el xito de los esfuerzos para mejorar la CX con la marca. 
      .col-10.col-lg.m-2.m-lg-2(data-aos="fade-down")
        img.img-a3.d-none.d-lg-block(src='@/assets/curso/temas/img52.svg')
        .row.h-100
          .col-12.px-5.pt-5.pb-5.pb-lg-0.bg8
            h4.mb-3 Ponerse en el lugar del cliente. 
            p.mb-0 La satisfaccin del cliente se basa en la empata. Si el cliente se ha contactado con la marca es porque requiere que soluciones algn problema o necesidad de forma rpida y efectiva.   

      .col-10.col-lg.m-2.m-lg-2(data-aos="fade-right")
        img.img-a3.d-none.d-lg-block(src='@/assets/curso/temas/img53.svg')
        .row.h-100
          .col-12.px-5.pt-5.pb-5.pb-lg-0.bg7
            h4.mb-3 Usar un lenguaje fcil de entender.  
            p.mb-0 Se requiere de tener claridad con el cliente. El lenguaje debe ser fcil de entender y al mismo tiempo, emptico. De esta forma, se pueden crear enlaces ms fuertes y leales con los clientes.

    .row.justify-content-center.align-items-center.mb-2           
      .col-lg-2.my-3 
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img54.svg', alt='')   
      .col-lg-10.my-3
        p(data-aos="fade-down") Dando continuidad a las dinmicas propias de la comunicacin, Castaeda (2016) dice que, adems de lograr una estrategia eficiente es importante hacer una planeacin efectiva. Los siguientes son ocho (8) pasos para planear la comunicacin con el cliente de manera exitosa:

    .titulo-sexto.color-acento-contenido.mb-3(data-aos="fade-right")
      p.mb-0 #[b Figura 3.]  8 pasos para construir una Estrategia de Comunicacin
    .row.justify-content-center.align-items-center.mb-5           
      .col-lg-10
        img.img-a.img-t.mb-3(data-aos="zoom-in")(src='@/assets/curso/temas/img55.svg', alt='')
        figcaption(data-aos="flip-down").mb-4 <b>Nota</b>. Imagen adaptada de 8 pasos para construir una Estrategia de Comunicacin. Vias (2016) 

    .tarjeta.tarjeta--azul.p-5.mb-5(data-aos="fade-left")
      SlyderA(tipo="b")
        .row.justify-content-center.align-items-start.p-3
          .col-lg-6.mb-4.mb-md-0
            h4 Paso 1: Anlisis del contexto. 
            p Se debe desarrollar un anlisis sobre los factores polticos, socioculturales, econmicos, ambientales, legales y tecnolgicos con relacin al impacto que pueden tener en el negocio. Para esto se puede usar el modelo PESTEL, que si est bien construido permite prever cmo se va a mover el mercado para planear las acciones de mercadeo que se pueden desarrollar.
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img56.png', alt='')  

        .row.justify-content-center.align-items-start.p-3
          .col-lg-6.mb-4.mb-md-0
            h4 Paso 2: Modelo de Comunicacin.  
            p Con base en el contexto empresarial, se deben establecer los protocolos de comunicacin para tomar decisiones, tener claro cmo funciona la resolucin de problemas, la promocin de transparencia, la gobernanza, la rendicin de cuentas y otros que pueden variar dependiendo del rtulo del negocio. Para que el protocolo sea funcional tiene que ser simple, debe transmitir cmo funciona la comunicacin al interior del negocio y debe instaurar tiempos.
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img57.png', alt='') 

        .row.justify-content-center.align-items-start.p-3
          .col-lg-6.mb-4.mb-md-0
            h4 Paso 3: Roles y Responsabilidades.  
            p Al establecer el modelo de comunicacin, se hace necesaria una asignacin de funciones al equipo interdisciplinario para que trabajen en el proceso de comunicaciones. Por ejemplo, un individuo que puede manejar todos los aspectos tcnicos del negocio, pero si no tiene carisma, es mejor no elegirlo para esta funcin. Pese a que es la persona que mejor analiza y explica la situacin, elegirlo como vocero sera un error. Lo mejor es que acompae o asesore a quien se encargue de esta labor.
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img58.png', alt='') 

        .row.justify-content-center.align-items-start.p-3
          .col-lg-6.mb-4.mb-md-0
            h4 Paso 4: Audiencias y Pblicos.  
            p Audiencia hace referencia a aquellos individuos que estarn expuestos a los medios de comunicacin usados por la marca; mientras que el Pblico es el cliente objetivo especfico al que se desea informar y que se interesa por la empresa, siendo muy importante porque posee la peculiaridad de participar de manera frecuente en redes sociales. Debe ser clasificado segn:
            ol.lista-ol--cuadro
              li 
                .lista-ol--cuadro__vineta
                  span 1
                p.mb-0 Necesidades.

              li 
                .lista-ol--cuadro__vineta
                  span 2
                p.mb-0 Intereses.

              li 
                .lista-ol--cuadro__vineta
                  span 3
                p.mb-0 Sinergias comunicacionales.             
            p Siempre se debe recordar que esto tiene que investigarse, o sea, que no se inventen datos. 
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img59.png', alt='')   

        .row.justify-content-center.align-items-start.p-3
          .col-lg-6.mb-4.mb-md-0
            h4 Paso 5: Lineamientos.  
            p Se refiere a los rasgos frecuentes y primordiales que admiten la definicin de una directriz o un plan que va a ayudar al mejoramiento de la reputacin empresarial; no confundir con los objetivos de operaciones. Por ejemplo, un lineamiento puede ser certificar que los inversionistas sepan sobre las ventajas de trabajar con la empresa; y un objetivo expone cmo conseguir ese lineamiento en marcos de accin desarrollados a corto plazo.
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img60.png', alt='')  

        .row.justify-content-center.align-items-start.p-3
          .col-lg-6.mb-4.mb-md-0
            h4 Paso 6: Resultados. 
            p Instaurar los objetivos de forma cuantificable, tanto en cantidad como calidad; esto es primordial porque as se pueden medir los impactos positivos o negativos que la marca va logrando. Estos resultados se deben alinear con los objetivos de la marca y provienen de lo cometido en la fase de lineamientos.
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img61.png', alt='') 

        .row.justify-content-center.align-items-start.p-3
          .col-lg-6.mb-4.mb-md-0
            h4 Paso 7: Tcticas y Acciones. 
            p Esta es una etapa operativa de la estrategia y se centra en los planes de comunicacin, que sujetarn los objetivos de operaciones para lograr los lineamientos comunicacionales. Estos planes deben ser desarrollados cada ao para actualizar el contexto e introducir los nuevos medios comunicacionales que la empresa vaya eligiendo. Por otro lado, se deben enfocar en la constante informacin, en establecer dilogos vinculantes y disminuir los riesgos.
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img62.png', alt='') 

        .row.justify-content-center.align-items-start.p-3
          .col-lg-6.mb-4.mb-md-0
            h4 Paso 8: Monitoreo y Evaluacin. 
            p Es el mtodo y las herramientas que se usarn para hacer medicin sobre el impacto de cada accin de comunicacin. Es vital no variar el mtodo porque de esta forma se puede hacer comparacin del progreso. Adems, con los indicadores hallados se puede saber cundo reorientar las acciones comunicacionales y establecer si se alcanzaron los resultados trazados.
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img63.png', alt='')

    Separador 
    #t_1_3.titulo-segundo.color-acento-contenido(data-aos="zoom-in-left")
      h2 1.3 Los clientes y el ecosistema digital 

    .row.justify-content-center.align-items-center.mb-5           
      .col-lg-4
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img64.png', alt='')                  
      .col-lg-8
        p(data-aos="fade-down") Quijano (2018) expresa que el cliente digital, tambin conocido como cliente 2.0, es el perfil de comprador <i>web</i> que ejecuta buena parte de sus compras a travs de internet. Aunque, en ocasiones, no termine comprando de forma digital. Posee unas caractersticas especficas, como, por ejemplo, tener un ciclo de compra que pasa por la revisin de muchos datos antes de darle clic al botn de comprar y tener unas necesidades muy concretas y personalizadas; estas necesidades deben ser enfocadas por las empresas en los procesos de venta, servicio y atencin al cliente. Las siguientes son algunas de las principales caractersticas del cliente digital: 

    TabsA.color-acento-contenido.mb-5
      .tarjeta.color-acento-botones.p-4(titulo="Se informa antes")
        .row
          .col-lg-8.mb-4.mb-lg-0
            h4 Se informa antes de formalizar una compra.
            p Visita blogs, redes sociales, compara productos y lee bastante sobre lo que le gusta.
          .col-lg-4
            figure
              img(src='@/assets/curso/temas/img65.png', style="width: 350px").m-auto   

      .tarjeta.color-acento-botones.p-4(titulo="Es inteligente")
        .row
          .col-lg-8.mb-4.mb-lg-0
            h4 Es inteligente 
            p Las marcas tienen el reto de alcanzar a un cliente que se informa y que es menos inmune a la publicidad tradicional. 
          .col-lg-4
            figure
              img(src='@/assets/curso/temas/img66.png', style="width: 350px").m-auto

      .tarjeta.color-acento-botones.p-4(titulo="Es riguroso y exigente")
        .row
          .col-lg-8.mb-4.mb-lg-0
            h4 Es riguroso y exigente 
            p Desea que la atencin sea buena, personalizada y rpida. No es de los que esperan y si en algunos clics no consigue lo que necesita se ir pronto a otro sitio <i>web</i>. 
          .col-lg-4
            figure
              img(src='@/assets/curso/temas/img67.png', style="width: 350px").m-auto  

      .tarjeta.color-acento-botones.p-4(titulo="Es diferente")
        .row
          .col-lg-8.mb-4.mb-lg-0
            h4 Es diferente 
            p Porque compra digitalmente no significa que no quiera que lo vean, lo que quiere es ahorrar tiempo y de pronto dinero. Requiere de una personalizacin mxima como cliente. 
          .col-lg-4
            figure
              img(src='@/assets/curso/temas/img68.png', style="width: 350px").m-auto  

      .tarjeta.color-acento-botones.p-4(titulo="De difcil retencin")
        .row
          .col-lg-8.mb-4.mb-lg-0
            h4 De difcil retencin 
            p Se hace compleja su fidelizacin porque siempre quiere experimentar cosas nuevas. Retenerlo no es un trabajo fcil. Pero, de hacerlo, ser un excelente cliente. 
          .col-lg-4
            figure
              img(src='@/assets/curso/temas/img69.png', style="width: 350px").m-auto  

      .tarjeta.color-acento-botones.p-4(titulo="Socializa")
        .row
          .col-lg-8.mb-4.mb-lg-0
            h4 Socializa 
            p Le gusta compartir sus experiencias, bien sea a travs de las redes sociales, un blog o el <i>WhatsApp</i>. Viraliza lo que le gusta de una marca, especialmente cuando tiene experiencias significativas. Pero si siente algo malo por parte de la marca (servicio, atencin, garanta, etc.), tambin lo har saber. 
          .col-lg-4
            figure
              img(src='@/assets/curso/temas/img70.png', style="width: 350px").m-auto  

      .tarjeta.color-acento-botones.p-4(titulo="Edades")
        .row
